<template>
  <div id="FindTheNumber" class="pb-16">
    
    <Alert ref="Alert"/>

    <InteractiveBord @dataForApp="updateTimeOver" @changeSetting="changeSettings()" :score="score" ref="InteractiveBord"/>

    <div class="w-full flex flex-col items-center font-bold">
      <div class="w-full flex flex-wrap max-w-5xl mt-3">
        <div class="md:p-1 w-1/6 h-12 sm:h-16 md:h-20" v-for="(item, index) in colors" :key="index">
        
          <div class="h-full w-full rounded-lg overflow-hidden cursor-pointer border" :style="'background-color:'+color[item]" @click="clickcolor(item)" />

        </div>
      </div>
    </div>
    
    <LevelsSelect @dataForApp="updateLevelValue" />

  </div>
</template>

<script>
import Alert from '../components/Alert'
import InteractiveBord from '../components/InteractiveBord'
import LevelsSelect from '../components/LevelsSelect'
export default {
  name: 'FindTheNumber',
  components: {
    Alert,
    InteractiveBord,
    LevelsSelect
  },
  data() {
    return {
      color:{},
      colors:{},

      score:0,

      stopGame:false,

      level:0,

      colorsEasy:[
        {0:"#ff2c2c",1:"#ff2c4c"},
        {0:"#e91e63",1:"#F44336"},
        {0:"#3f51b5",1:"#3F518E"},
        {0:"#8bc34a",1:"#8BC385"},
        {0:"#9E9E9E",1:"#9EA0AE"},
        {0:"#ff5722",1:"#FF5764"},
        {0:"#673ab7",1:"#673A93"},
        {0:"#03a9f4",1:"#2196f3"},
        {0:"#2196f3",1:"#03a9f4"},
        {0:"#009688",1:"#00966A"},
        {0:"#b53f3f",1:"#B53F5B"},
        {0:"#b0d420",1:"#B0D476"},
        {0:"#20d4aa",1:"#32f5c7"},
        {0:"#d420c6",1:"#ec2dde"},
        {0:"#2026d4",1:"#262df1"},
      ],
      colorsMedium:[
        {0:"#ff0303",1:"#e80000"},
        {0:"#673ab7",1:"#7642d2"},
        {0:"#ff5722",1:"#FF574A"},
        {0:"#cddc39",1:"#c2d036"},
        {0:"#009688",1:"#0096A3"},
        {0:"#9e9e9e",1:"#9E9E97"},
        {0:"#607d8b",1:"#607D6F"},
        {0:"#009688",1:"#009672"},
        {0:"#795548",1:"#67473b"},
        {0:"#bf6748",1:"#BF676D"},
        {0:"#3f51b5",1:"#3F5198"},
        {0:"#03a9f4",1:"#03A9C8"},
        {0:"#435e6b",1:"#435E54"},
        {0:"#717171",1:"#71715F"},
        {0:"#ff9800",1:"#FF984F"},
      ],
      colorsHard:[
        {0:"#f44336",1:"#ec3c2f"},
        {0:"#e91e63",1:"#e63772"},
        {0:"#9c27b0",1:"#ab2bc1"},
        {0:"#673ab7",1:"#673ab7"},
        {0:"#3f51b5",1:"#3c4ca7"},
        {0:"#2196f3",1:"#38a0f3"},
        {0:"#00bcd4",1:"#06c4dc"},
        {0:"#4caf50",1:"#4aa54d"},
        {0:"#8bc34a",1:"#94d050"},
        {0:"#cddc39",1:"#d2e04a"},
        {0:"#ffeb3b",1:"#f7e43d"},
        {0:"#ffc107",1:"#f7ba04"},
        {0:"#ff5722",1:"#f96537"},
        {0:"#795548",1:"#6b4e44"},
        {0:"#607d8b",1:"#677f8a"},
      ]
    }
  },
  created() {
    this.MStart();
  },
  methods: {
    MStart(){
      this.callAddColor();
    },
    clickcolor(item){
      var vm=this;
      var alert = vm.$refs.Alert;
      if(vm.stopGame)return;
      if(!vm.$refs.InteractiveBord.timerInterval) vm.$refs.InteractiveBord.startTimer();
      if (item == 1) {
        vm.score += 2;
        alert.alertShow("+2")
        console.log("+2")
      } else {
        if(vm.score > 0)vm.score -= 1;
        if(vm.score > 0)alert.alertShow("-1","bg-red-500")
        else alert.alertShow("0","bg-red-500")
        console.log("-1")
      }

      this.callAddColor();
    },
    callAddColor(){
      //this.addColor()
      this.addRandomColor()
    },
    addColor(){
      this.colors={};
      for(var a=[],i=0;i<36;i++)a[i]=0;

      a[0] = 1;
      
      for (let n = 0; n < a.length; n++) {
        this.colors[n] = a[n];
      }

      this.color = this.getColor();
    },
    addRandomColor(){
      this.colors={};
      for(var a=[],i=0;i<36;i++)a[i]=0;

      a[Math.floor(Math.random() * a.length)] = 1;
      
      for (let n = 0; n < a.length; n++) {
        this.colors[n] = a[n];
      }

      this.color = this.getColor();
    },

    getColor(){
      var colorByLevel = this.colorsEasy;
      if (this.level == 0) colorByLevel = this.colorsEasy;
      else if (this.level == 1) colorByLevel = this.colorsMedium;
      else if (this.level == 2) colorByLevel = this.colorsHard;
      var color = colorByLevel[Math.floor(Math.random() * this.colorsEasy.length)];
      return color;
    },


    updateTimeOver(){
      this.stopGame = true;
      this.$refs.Alert.alertTimeOver();
    },
    updateLevelValue(value){
      this.level = value;
      this.changeSettings();
    },

    changeSettings(){
      var vm = this;
      var level = vm.level;
      vm.$refs.InteractiveBord.stopTimer(1);
      Object.assign(vm.$data,vm.$options.data.call(vm))
      vm.level = level;
      vm.MStart();
    }
  }
}
</script>

